.first-content {
    /* padding-top: 7%; */
    display: flex;
    height: 40rem;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.first-content .left-content {
    width: 50%;
    margin-top: -10%;
    margin-left: 5%;
    box-sizing: border-box;
    display: block;
    text-align: left;

}

.left-content h1 {
    font-size: 5rem;
    max-width: 750px;
    /* margin: 0%; */
    text-transform: uppercase;
}

.left-content p {
    margin-top: -5%;
    max-width: 600px;
}

.first-content .right-content {
    min-width: 50%;
    height: 90%;
    /* margin-top: -10%; */
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("../../images//Frame-2.png");
    background-size: contain;

    background-repeat: no-repeat;

}
.First-content h1 {
    width: 30%;
}

.Left-side {
    height: 40rem;
    width: 50%;
}

.Img {
    height: 30rem;
    width: 50rem;
    /* float: right; */
}

.Right-side {
    border-radius: 30px;
    height: 600px;
}


.Login-side h2 {
    color: #000080;
    font-weight: bold;
}

.Input input {
    width: 350px;
    height: 60px;
    padding-left: 20px;
    height: 40px;
    border-radius: 5px;
    font-size: 16px;
    border-width: 1px;
}

.input-email {
    margin-top: 20px;
}

.input-password {
    margin-top: 20px;
}

.Checkbox-Accept {
    margin-top: 15px;
    color: black;
}

.Checkbox-Accept p {
    margin-right: 120px;
    margin-top: 0px;
    font-size: 15px;
    font-weight: normal;
    float: right;
    color: black;
    width: auto;
    height: auto;
}

.Checkbox {
    margin-top: 0;
    border-radius: 5px;
    height: 20px;
    width: 20px;
}

/*Nút đăng ký*/
.btn-Register {
    cursor: pointer;
    border-style: none;
    /* border-color: blue; */
    border-radius: 10px;
    color: white;
    font-weight: bold;
    background-color: purple;
    width: 375px;
    font-size: 20px;
    height: 50px;
}

.seccond-content{
    display: flex;
    /* justify-content: center; */
    flex-direction: column;
    align-items: center;
    /* background-color: red; */
}
.seccond-content-icon{
    border-radius: 50%;
    width: 120px;
    background-color: white;
    text-align: center;
}
